import React, { Component } from 'react'
import Services from '../services/bookclub.services'



class BookclubForm extends Component {

    constructor(props) {
        super(props)
        this.state = {
            name: '',
            description: '',
            city: '',
            creator: '',
            contact: '',
            creator_id: this.props.creatorid.data._id,
            books: ''
        }
        this.service = new Services()
    }

  
   
    handleChangeInput = e => this.setState({ [e.target.name]: e.target.value })

    handleFormSubmit = e => {
        e.preventDefault()
        this.service.postBookclub(this.state)
            .then(x => {
                this.props.closeModal()
                this.props.updateBookclubList()
            })
            .catch(err => console.log('error', err))
    }

    render() {

    // console.log("bookclubform - this.props.creatorid: ", this.props.creatorid.data._id)
        // let dataid= this.props.creatorid.data._id
        return (
            <>
                <h4>Nuevo Club de Libro</h4>

                <hr></hr>

                <form onSubmit={this.handleFormSubmit}>
                    <div className="form-group">
                        <label htmlFor="input-nombre">Nombre</label>
                        <input name="name" type="text" className="form-control" id="input-nombre" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-description">Descripción</label>
                        <input name="description" type="text" className="form-control" id="input-description" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-city">Ciudad</label>
                        <input name="city" type="text" className="form-control" id="input-city" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-creator">Creador</label>
                        <input name="creator" type="text" className="form-control" id="input-creator" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-contact">Informacion de contacto</label>
                        <input name="contact" type="text" className="form-control" id="input-contact" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                    {/* <input name="creator_id" type="hidden" className="form-control" id="input-creatorid" value="dataid" /> */}
                        {/* <input name="creator_id" type="hidden" className="form-control" id="input-creatorid" value={this.props.creatorid.data._id} /> */}
                        </div>
                    <button type="submit" className="btn btn-dark btn-sm">Crear</button>
                    <button className="btn btn-dark btn-sm" onClick={this.props.closeModal}>Cerrar</button>
                </form>
            </>
        )
    }

}

export default BookclubForm