import React, { Component } from 'react'
import Services from '../services/bookclub.services'

import MyBookclubCard from './MyBookClubCard'
import ScheduleForm from './ScheduleForm'

import Dialog from '@material-ui/core/Dialog';


class MyClubs extends Component {

    constructor(props) {
        super(props)
        this.state = { bookclubs: [], showModal: false }
        this.services = new Services()
    }

    componentDidMount = () => this.updateList()

    updateList = () => {
        // console.log('actualizando lista')
        this.services.getMyBookclub(this.props.loggedInUser.data._id)
            .then(response => this.setState({ bookclubs: response.data }))
            .catch(err => console.log(err))
    }

    handleModalOpen = () => this.setState({ showModal: true })
    handleModalClose = () => this.setState({ showModal: false })

    render() {
 //       console.log("this is at MyClubs.js", this.state.bookclubs)
        return (
            <>
                <div className="container">
                <h1>Mis Clubs de Libro</h1>
                    
            

                

                    {/* {this.props.loggedInUser && <button className="btn btn-dark btn-big" onClick={this.handleModalOpen}>Iniciar un club nuevo</button>} */}

                    <div className="row coasters-list">

        {this.state.bookclubs.map(bookclub => {
return (
        <>         
    
        <Dialog onClose={this.handleModalClose} open={this.state.showModal} className="modal-form">
            <ScheduleForm userInSession={this.props.loggedInUser} closeModal={this.handleModalClose} updateBookclubList={this.updateList} creatorid={this.props.loggedInUser} club={bookclub}/>
        </Dialog>
        <MyBookclubCard  key={bookclub._id} {...bookclub} />
        <button onClick={this.handleModalOpen}>Programar</button>   
        </>)
        })}

                    </div>
                </div>
            </>
        )
    }
}


export default MyClubs