import axios from 'axios'

export default class Services {

    constructor() {

        this.service = axios.create({
            baseURL: 'http://localhost:5000/api/',
            withCredentials: true
        })
    }

    getBookclubs = () => this.service.get('getAllBookclubs')
    getMyBookclub = user_id => this.service.get(`getMyBookclub/${user_id}`)
    getOneBookclub = id => this.service.get(`getOneBookclub/${id}`)
    postBookclub = theNewBookclub => this.service.post(`postBookclub`, theNewBookclub)
    addClubMember = theNewBookclub => {
        // console.log(theNewBookclub)
        return this.service.post(`addClubMembers`, theNewBookclub)
    }

}