import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Services from '../services/bookclub.services'




class BookclubDetail extends Component {

    constructor(props) {
        super(props)
        this.state = { bookclub: {} }
        this.service = new Services()
    }

    componentDidMount() {
         this.service.getOneBookclub(this.props.match.params.id)
        
            .then(
                response => this.setState({ bookclub: response.data }))
            .catch(err => console.log('err', err))
    }

    render() {
        return (
            <div className="container">
                <article className="coaster-detail">
                    <div className="row justify-content-center">
                        <div className="col-md-6">
                            <h1>Detalles de {this.state.bookclub.name}</h1>
                            <p><strong>Descripción:</strong> {this.state.bookclub.description}</p>
                            <hr></hr>
                            <p><small>Ciudad:</small> {this.state.bookclub.city}</p>
                          
                            <p>Libros leidos: {this.state.books} | {this.state.meetingdates}</p>
                            <p>Para mayor informacion ponte en contacto con {this.state.bookclub.creator} </p>
                            <p>Su correo {this.state.bookclub.contact} </p>
                            <Link className="btn btn-big btn-dark" to="/bookclubs">Volver</Link>
                        </div>
                        
                    </div>
                </article>
            </div>
        )
    }

}

export default BookclubDetail