import React, {Component} from 'react';
import './App.css';
import Login from './components/auth/login'
import Signup from './components/auth/signup'
import { Switch, Route } from 'react-router-dom'
import NavBar from './components/Navbar'
import AuthServices from './services/auth.services'
import BookclubsList from './components/Bookclub-list'
import MyClubs from './components/MyClubs'
import ProtectedRoute from './components/routes/ProtectedRoute'
import ScheduleForm from './components/ScheduleForm'
import BookclubDetail from './components/Bookclub-detail'
import MembershipForm from './components/MembershipForm'
//Switch y Route
class App extends Component {

  constructor() {
    super()
    this.state = { loggedInUser: null }
    this.authServices = new AuthServices()
  }

  setTheUser = user => {
    this.setState({ loggedInUser: user })
    console.log("Un componente ha cambiado el usuario en App:", this.state.loggedInUser)
  }

  fetchUser = () => {
    if (this.state.loggedInUser === null) {
      this.authServices.loggedin()
        .then(response => this.setState({ loggedInUser: response }))
        .catch(x => this.setState({ loggedInUser: false }))
    }
  }

  render() {

   
    if (this.state.loggedInUser) {
   
     return  ( <div>
       
       <NavBar setUser={this.setTheUser} userInSession={this.state.loggedInUser} />
          <Switch>

            {/* <Route path="/bookclub"  /> */}
            <Route path="/bookclub/:id" exact component={BookclubDetail} />
            <Route path="/membership" exact render={(match) => <MembershipForm {...match} userInSession={this.state.loggedInUser} />} />
            <Route path="/bookclubs" exact render={() => <BookclubsList userInSession={this.state.loggedInUser} />} />
            {/* <Route path="/bookclub/:id" exact component={BookclubDetail} /> */}
            <ProtectedRoute path='/myclubs' userInSession={this.state.loggedInUser} component={MyClubs} />
            <Route path="/schedule"  render={() => <ScheduleForm userInSession={this.state.loggedInUser} />} />       
            
          </Switch>
        </div>
     )
    }
    else
    return  ( <div>
       
      <NavBar setUser={this.setTheUser} userInSession={this.state.loggedInUser} />
         <Switch>

           {/* <Route path="/bookclub"  /> */}
           <Route path="/bookclub/:id" exact component={BookclubDetail} />
           <Route path="/bookclubs" exact render={() => <BookclubsList userInSession={this.state.loggedInUser} />} />
           {/* <ProtectedRoute path='/myclubs' userInSession={this.state.loggedInUser} component={MyClubs} /> */}
           <Route path="/membership" exact render={match => <MembershipForm {...match} userInSession={this.state.loggedInUser} />} />
           {/* <Route path="/bookclub/:id" exact component={BookclubDetail} /> */}
           <Route path="/signup" exact render={match => <Signup {...match} setUser={this.setTheUser} />} /> 
           <Route path="/login" exact render={match => <Login {...match} setUser={this.setTheUser} />} />
         </Switch>
       </div>
    )
}
}


export default App;
