import React, { Component } from 'react'
import AuthServices from '../../services/auth.services'
import '../../styles/signup.css';

class Signup extends Component {

    constructor(props) {
        super(props)
        this.state = {
            username: '',
            password: '',
            email: ''
        }
        this.authServices = new AuthServices()
    }

    handleInputChange = e => {
        const { name, value } = e.target
        this.setState({ [name]: value })
    }

    handleFormSubmit = e => {
        e.preventDefault()
        const { username, password, email } = this.state
        this.authServices.signup(username, password, email)
            .then(theNewUser => {
                this.setState({
                    username: '',
                    password: '',
                    email: ''
                })
                this.props.setUser(theNewUser)
                this.props.history.push('/bookclubs')
            })
            .catch(err => console.log({err}))
    }

    render() {

        return (

           
        <div className="container signup-form">
        <form className="px-3 py-3 form-control form-control-sm" onSubmit={this.handleFormSubmit}>
         <div className="form-group">
         <label for="input-username">Usuario:</label>
         <input type="text" className="form-control" name="username" 
          value={this.state.username} onChange={this.handleInputChange}  placeholder="Su nombre de usuario"/>
         </div>
        <div className="form-group">
        <label for="input-password">Contraseña</label>
        <input type="password" className="form-control" name="password" value={this.state.password} onChange={this.handleInputChange} placeholder="Contraseña"/>
        </div>
        <div className="form-group">
        <label for="input-email">Correo</label>
         <input type="email" className="form-control" name="email"  value={this.state.email} onChange={this.handleInputChange} placeholder="Correo"/>
          </div>
    <button type="submit" className="btn btn-primary">Registrar</button>
  </form>
  </div>
         //   {/* {/* // <div className="container">
            //     <h1>Registro de usuario</h1>
            //     <form onSubmit={this.handleFormSubmit}>
            //         Usuario: <input name="username" type="text" value={this.state.username}  */}
       //     {/* //         onChange={this.handleInputChange} /> <br></br>
            //         Contraseña: <input name="password" type="password" value={this.state.password} onChange={this.handleInputChange} /> <br></br>
            //         Correo: <input name="email" type="email" value={this.state.email} onChange={this.handleInputChange} /> <br></br>
            //         <input type="submit" value="Registrar" />
            //     </form>
            // </div> */} 

        )
    }
}

export default Signup


  