import React, { Component } from 'react'
import Services from '../services/meeting.services'



class ScheduleForm extends Component {

    constructor(props) {
        super(props)
        this.state = {
            clubname: '',
            location: '',
            book: '',
            bookauthor: '',
            date: '',
            time: '',
            bookclub_id: this.props.userInSession.data._id
        }
        this.service = new Services()
        
    }

   
    componentDidMount() {
        this.setState({clubname: this.props.club.name}) 
   }
     
 
    handleChangeInput = e => this.setState({ [e.target.name]: e.target.value })

    handleFormSubmit = e => {
        e.preventDefault()
        console.log("this.state at ScheduleForm: ",this.state)
        this.service.postMeeting(this.state)
            .then(x => {
                console.log("is this passed to Addmeeting? ",this.state)
                this.service.addMeetingtoClub(this.state)
                    .then(x => console.log(x))
                    .catch(err => console.log({err}))
                this.props.closeModal()
              
         //       this.setState = {}
            })
    //         .then(x => {
    //             console.log(this.state)
    //   //          this.props.closeModal()
    //             this.props.updateMeetingList()
    //      //       this.setState = {}
    //         })
            .catch(err => console.log('error', {err}))
    }

    render() {
     //   console.log("props: ", this.props)
        
    // console.log("bookclubform - this.props.creatorid: ", this.props.creatorid.data._id)
        // let dataid= this.props.creatorid.data._id
        return (
            <>
                <h4>Nueva Reunión</h4>

                <hr></hr>

                <form onSubmit={this.handleFormSubmit}>
                    {/* <div className="form-group">
                        <label htmlFor="input-nombre">Nombre</label>
                        <input name="name" type="text" className="form-control" id="input-nombre" onChange={this.handleChangeInput} />
                    </div> */}
                    <div className="form-group">
                        <label htmlFor="input-location">Dirección</label>
                        <input name="location" type="text" className="form-control" id="input-location" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-book">Libro</label>
                        <input name="book" type="text" className="form-control" id="input-book" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-creator">Autor</label>
                        <input name="bookauthor" type="text" className="form-control" id="input-bookauthor" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-date">Fecha</label>
                        <input name="date" type="date" className="form-control" id="input-date" onChange={this.handleChangeInput} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="input-date">Hora</label>
                        <input name="time" type="time" className="form-control" id="input-time" onChange={this.handleChangeInput} />
                    </div>
                   
                    <button type="submit" className="btn btn-dark btn-sm">Crear</button>
                
                </form>
            </>
        )
    }

}

export default ScheduleForm