import axios from 'axios'

export default class Services {

    constructor() {

        this.service = axios.create({
            baseURL: 'http://localhost:5000/api/',
            withCredentials: true
        })
    }

    getMeetings = () => this.service.get('getAllMeetings')
    getMyMeeting = bookclub_id => this.service.get(`getMyMeeting/${bookclub_id}`)
    getOneMeeting = id => this.service.get(`getOneMeeting/${id}`)
    postMeeting = theNewMeeting => this.service.post(`postMeeting`, theNewMeeting)
    addMeetingtoClub = theNewBookclub => { 
        console.log('service de addmeeting', theNewBookclub)
        return this.service.post(`addMeetingtoClub`, theNewBookclub)}
}