import React, { Component } from 'react'
import Services from '../services/bookclub.services'

import BookclubCard from './Bookclub-card'
import BookclubForm from './Bookclub-form'

import Dialog from '@material-ui/core/Dialog';


class BookclubList extends Component {

    constructor() {
        super()
        this.state = { bookclubs: [], showModal: false }
        this.services = new Services()
    }

    componentDidMount = () => this.updateList()

    updateList = () => {
        this.services.getBookclubs()
            .then(response => this.setState({ bookclubs: response.data }))
            .catch(err => console.log(err))
    }

    handleModalOpen = () => this.setState({ showModal: true })
    handleModalClose = () => this.setState({ showModal: false })


    render() {
  //      console.log(this.state.bookclubs)
        return (
            <>
                <div className="container">

                    <Dialog onClose={this.handleModalClose} open={this.state.showModal} className="modal-form">
<BookclubForm closeModal={this.handleModalClose} updateBookclubList={this.updateList} creatorid={this.props.userInSession}/>
                    </Dialog>

                    <h1>Lista de Clubs de Libro</h1>

                    {this.props.userInSession && <button className="btn btn-dark btn-big" onClick={this.handleModalOpen}>Iniciar un club nuevo</button>}

                    <div className="row coasters-list">

                        {this.state.bookclubs.map(bookclub => <BookclubCard key={bookclub._id} {...bookclub} />)}

                    </div>
                </div>
            </>
        )
    }
}


export default BookclubList